"use client";
import { computeSubtypesForIssue } from "@/lib/computeSubtypes";
import clsx from "clsx";

export default function SubtypeScreen({ category, issue, catalog, selected, onToggle, onNext }: any) {
  const subtypes = computeSubtypesForIssue(category, issue, catalog);

  return (
    <div className="space-y-6">
      <h2 className="text-xl font-semibold">
        {category} → {issue} → Select Subtypes
      </h2>

      <div className="flex flex-wrap gap-3">
        {subtypes.map(sub => (
          <button
            key={sub}
            onClick={() => onToggle(sub)}
            className={clsx(
              "px-4 py-2 border rounded-lg",
              selected.includes(sub)
                ? "bg-green-600 text-white border-green-600"
                : "bg-white border-zinc-300"
            )}
          >
            {sub}
          </button>
        ))}
      </div>

      {selected.length > 0 && (
        <button
          onClick={onNext}
          className="mt-6 px-5 py-3 bg-zinc-900 text-white rounded-lg"
        >
          Next
        </button>
      )}
    </div>
  );
}
