"use client";
import { computeIssuesForCategory } from "@/lib/computeIssues";

export default function IssueScreen({ category, catalog, onSelect }: any) {
  const issues = computeIssuesForCategory(category, catalog);

  return (
    <div className="space-y-6">
      <h2 className="text-xl font-semibold">{category} → Select Issue</h2>

      <div className="flex flex-wrap gap-3">
        {issues.map(issue => (
          <button
            key={issue}
            onClick={() => onSelect(issue)}
            className="px-5 py-2 bg-blue-600 text-white rounded-lg hover:bg-blue-500"
          >
            {issue}
          </button>
        ))}
      </div>
    </div>
  );
}
