"use client";

import { useEffect, useState } from "react";
import { fetchCatalog } from "@/app/api/backend";

import CategoryScreen from "./CategoryScreen";
import IssueScreen from "./IssueScreen";
import SubtypeScreen from "./SubtypeScreen";
import FinalScreen from "./FinalScreen";

export default function QueryFlow() {
  const [catalog, setCatalog] = useState<any>(null);

  const [step, setStep] = useState(1);
  const [category, setCategory] = useState("");
  const [issue, setIssue] = useState("");
  const [selectedSubtypes, setSelectedSubtypes] = useState<string[]>([]);

  // Load catalog once
  useEffect(() => {
    fetchCatalog().then(setCatalog);
  }, []);

  if (!catalog) return <div>Loading…</div>;

  const restart = () => {
    setStep(1);
    setCategory("");
    setIssue("");
    setSelectedSubtypes([]);
  };

  return (
    <div className="p-10 space-y-10">
      {step === 1 && (
        <CategoryScreen
          catalog={catalog}
          onSelect={(cat: string) => {
            setCategory(cat);
            setStep(2);
          }}
        />
      )}

      {step === 2 && (
        <IssueScreen
          category={category}
          catalog={catalog}
          onSelect={(iss: string) => {
            setIssue(iss);
            setStep(3);
          }}
        />
      )}

      {step === 3 && (
        <SubtypeScreen
          category={category}
          issue={issue}
          catalog={catalog}
          selected={selectedSubtypes}
          onToggle={(sub: string) =>
            setSelectedSubtypes(prev =>
              prev.includes(sub) ? prev.filter(x => x !== sub) : [...prev, sub]
            )
          }
          onNext={() => setStep(4)}
        />
      )}

      {step === 4 && (
        <FinalScreen
          category={category}
          issue={issue}
          subtypes={selectedSubtypes}
          onRestart={restart}
        />
      )}
    </div>
  );
}
